<p class="sub-title">my work 02;</p><h2><div class="strong">NoQ Product Page. 2020.</div> Developed for Redso using <a href="https://vuejs.org" target="_blank">Vue</a>, <a href="https://nuxtjs.org/" target="_blank">Nuxt</a> and <a href="https://strapi.io/" target="_blank">Strapi</a></h2><div class="flex images"><div class="flex-full"><img class="small" src="@/assets/images/workvalue-sm.jpg"></div><div class="screenshot flex-zero"><img src="@/assets/images/workvalue.jpg"></div></div><div class="description"><p class="sub-title">description;</p><p class="content">Built for mobile and web platform. A causal game where player need to drag their fingers or cursors on the screen to apply force to the character. Mainly using <span class="strong">Unity's 2D Physics Engine</span> and <span class="strong">Sprite System</span> to build the game.</p></div><div class="link"><p class="sub-title">link;</p><p class="content"><a href="https://nuxtjs.org/" target="_blank">NoQ Url</a></p></div>